//frontend/pages/messages/index.js
import { useEffect, useState } from "react";
import api from "../../utils/api";

export default function Messages() {
  const [messages, setMessages] = useState([]);
  const [text, setText] = useState("");
  const [userId, setUserId] = useState(null);
  const [userEmail, setUserEmail] = useState(""); // নতুন

  useEffect(() => {
    if (typeof window !== "undefined") {
      const id = localStorage.getItem("userId");
      setUserId(id);
    }
  }, []);

  // Fetch email after userId is set
  useEffect(() => {
    if (userId) fetchUserEmail();
  }, [userId]);

  const fetchUserEmail = async () => {
    try {
      const res = await api.get(`/users/${userId}`);
      setUserEmail(res.data.email);
      loadMessages();
    } catch (err) {
      console.error(err);
    }
  };

  const loadMessages = async () => {
    try {
      const res = await api.get(`/messages/${userId}`);
      setMessages(res.data);
    } catch (err) {
      console.error(err);
    }
  };

  const sendMessage = async () => {
    if (!text || !userId) return;
    try {
      await api.post("/messages/send", { userId, text });
      setText("");
      loadMessages();
    } catch (err) {
      console.error(err);
    }
  };

  if (!userId) return <h2>Please login first.</h2>;

  return (
    <div>
      <h2>Your Chat {userEmail && `(User: ${userEmail})`}</h2>
      {messages.map((m) => (
        <p key={m._id}>
          <b>{m.sender}:</b> {m.text}
        </p>
      ))}
      <input
        value={text}
        onChange={(e) => setText(e.target.value)}
        placeholder="Type message"
      />
      <button onClick={sendMessage}>Send</button>
    </div>
  );
}
